.weather-summary {
  display: grid;
  padding-bottom: 2em;
  .current-city{
    font-size: 26px;
    font-weight: 600;
  }
  .sub-header{
    border-top: 1px solid gray;
  }
  .forecast-group-wrapper {
    overflow-y: hidden;
    overflow-x: auto;
    max-width: 100%;
    height: 100%;

    .forecast-group-hourly {
      width: 2110px;
      overflow-y: auto;
      display: grid;
      justify-content: center;
      grid-template-columns: repeat(24, 5em);
      grid-template-rows: 100%;
      column-gap: 0.5em;

      .forecast-item {
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        border: 1px solid gray;
      }
    }
  }

  .forecast-group-week {
    width: 1000px;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(7, 8em);
    grid-template-rows: 100%;
    column-gap: 1em;
    height: 100%;
    .forecast-item {
      box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
      border: 1px solid gray;
    }
  }

  .forecast-current {
    font-size: 20px;
    img{
      width: 70px;
      height: 70px;
    }
    .main-temp {
      font-size: 50px;
    }
  }

  //Hide scrollbar for Chrome, Safari and Opera
  .forecast-group-wrapper::-webkit-scrollbar {
    display: none;
  }

  // Hide scrollbar for IE, Edge and Firefox
  .forecast-group-wrapper {
    -ms-overflow-style: none; // !* IE and Edge *!
    scrollbar-width: none; // !* Firefox *!
  }
}

@media only screen and (max-width: 600px) {
  .forecast-group-wrapper {
    overflow-y: auto;

  }
}